.App {
  text-align: left;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body{
	margin:20px;
	
}


form{
	display:inline;
}

form div{
	margin-left:20px;
	display:inline-block;
	
}


table {
 border-collapse:collapse;
}
table th{
	background-color:#ddd;
}

table td,table th{
  white-space: nowrap;
  padding:10px;
   border:1px solid #ccc;
}
table td > div{
	margin-bottom:10px;
	border:1px solid #ccc;
	padding:10px;
	height:10px;
	overflow:hidden;
}
table tr:nth-child(2n){
	background-color:#f0f0f0;
}

img.floatRight{
	float:right;
	
}